.repository {
   width: 260px;
   border: #979797 solid 1pt;
   border-radius: 5pt;
   padding: 10px 15px;
   display: inline-block;
   margin-right: 20px;
   margin-top: 10px;
}

.repository a {
   text-align: left;
   text-decoration: none;
   color: black;
}

.repository img {
   margin-right: 10px;
}

.repository-title {
   font-weight: 600;
   font-size: 12pt;
   line-height: 0;
   font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
   display: inline-block;
   margin: 0;
   margin-right: 5px;
}

.repository-desc {
   font-size: 10pt;
   font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
   margin: 0;
   margin-top: 10px;
}



