.exp-year {
   list-style: none;
   padding: 0;
   margin: 0;
}

.exp-year li {
   display: inline-block;
   padding-right: 10px;
}

.exp-year li a {
   text-decoration: none;
   color: rgba(191, 206, 212, 1.000);
}

.exp-year-selected {
   color: #3d7afb !IMPORTANT;
}

.exp-title {
   font-size: 11pt;
}

.exp-duration {
   font-size: 10pt;
   font-family: 'Source sans pro', sans-serif;
   line-height: 0;
   padding: 0;
}

.exp-desc {
   font-size: 11pt;
   padding-right: 20px;
}

.exp-desc a {
   text-decoration: none;
   color: #3d7afb;
}

.exp-desc ul {
   list-style-position: inside;
   padding-left: 0;
}



