.iOS-App {
  width: 100%;
  height: 85px;
  display: inline-block;
  color: black;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  text-decoration: none;
}

.iOS-App img {
  width: 25%;
  display: inline-block;
  float: left;
  max-width: 75px;
}

.iOS-App div {
  margin-left: 4% !important;
  float: left;
  width: 71%;
}

.iOS-App div h3, .iOS-App div h4 {
  line-height: 0;
  text-align: left;
}

.iOS-App div h3 {
  font-family: "Lato", sans-serif;
  font-size: 15pt;
  margin-bottom: 0;
}

.iOS-App div h4 {
  font-family: "Lato", sans-serif;
  font-size: 13pt;
  font-weight: 300;
  line-height: 13pt;
  margin-top: 10pt;
}

@media screen and (max-width: 495px) {
   .iOS-App div {
      width: 100%;
   }
}



