#launch-list {
  width: 95%;
  text-align: center;
  padding: 10px 0;
  border: 2px #ECECEC solid;
  border-radius: 8px;
  margin: 0 auto;
  margin-top: 65px;
  margin-bottom: 65px;
}
#launch-list div {
  padding: 5px 0;
  text-align: center;
  display: inline-block;
}
#launch-list div p {
  vertical-align: middle;
  display: inline-block;
  width: 30%;
  max-width: 400px;
  min-width: 300px;
  margin-right: 5%;
  line-height: 25px;
  font-size: 15pt;
  font-family: "Lato", sans-serif;
  font-weight: 17pt;
}
#launch-list div form {
  margin-top: 5px;
}

input.button {
  margin: 0;
  margin-left: -21px;
  padding: 0;
  height: 29px;
  width: 75px;
  background-color: black;
  color: white;
  border: 0;
  border-radius: 0 5px 5px 0;
  -webkit-appearance: none;
  cursor: pointer;
}
input.button:hover {
  background-color: #585858;
}

input {
  background-color: white;
  width: 190px;
  border-radius: 5px 0 0 5px;
  font-size: 10pt;
  padding: 0;
  padding-left: 15px;
  margin: 0;
  background-color: #EFEFEF;
  border: 0;
  height: 29px;
  outline: 0;
}

#mc-embedded-subscribe-form div {
  display: inline-block;
}



