#header {
   width: 100%;
   height: 250px;
   background-color: #FCFCFC;
   display: -ms-flexbox;
   display: flex;
   -ms-flex-pack: center;
       justify-content: center;
   -ms-flex-align: center;
       align-items: center;
}

#header div h1, h2 {
   text-align: center;
}

#header h2 {
   font-weight: 400;
   font-size: 12pt;
   line-height: 12pt;
   font-family: 'Montserrat', sans-serif;
   color: #C4C4C4;
}

#header h1 {
   font-family: 'Source Sans Pro', sans-serif;
   text-transform: uppercase;
   font-size: 35pt;
   letter-spacing: 0.15em;
   line-height: 35pt;
   padding: 0;
}



