.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
   background-color: #222;
   height: 150px;
   padding: 20px;
   color: white;
}

.App-title {
   font-size: 1.5em;
}

.App-intro {
   font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

h1, h2, h3, h4 {
   font-family: 'Montserrat', sans-serif;
}

.section-header {
   text-transform: uppercase;
   letter-spacing: 1px;
   margin: 50px  0 25px 0;
}
/*
Simple Grid
Learn More - http://dallasbass.com/simple-grid-a-lightweight-responsive-css-grid/
Project Page - http://thisisdallas.github.com/Simple-Grid/
Author - Dallas Bass
Site - dallasbass.com
*/

*, *:after, *:before {
   -webkit-box-sizing: border-box;
   box-sizing: border-box;
}

body {
   margin: 0px;
}

[class*='col-'] {
   float: left;
   padding-right: 20px;
   /* column-space */
}

.grid {
   width: 100%;
   max-width: 1140px;
   min-width: 755px;
   margin: 0 auto;
   overflow: hidden;
}

.grid:after {
   content: "";
   display: table;
   clear: both;
}

.grid-pad {
   padding-top: 20px;
   padding-left: 20px;
   /* grid-space to left */
   padding-right: 0px;
   /* grid-space to right: (grid-space-left - column-space) e.g. 20px-20px=0 */
}

.push-right {
   float: right;
}

/* Content Columns */

.col-1-1 {
   width: 100%;
}

.col-2-3, .col-8-12 {
   width: 66.66%;
}

.col-1-2, .col-6-12 {
   width: 50%;
}

.col-1-3, .col-4-12 {
   width: 33.33%;
}

.col-1-4, .col-3-12 {
   width: 25%;
}

.col-1-5 {
   width: 20%;
}

.col-1-6, .col-2-12 {
   width: 16.667%;
}

.col-1-7 {
   width: 14.28%;
}

.col-1-8 {
   width: 12.5%;
}

.col-1-9 {
   width: 11.1%;
}

.col-1-10 {
   width: 10%;
}

.col-1-11 {
   width: 9.09%;
}

.col-1-12 {
   width: 8.33%
}

/* Layout Columns */

.col-11-12 {
   width: 91.66%
}

.col-10-12 {
   width: 83.333%;
}

.col-9-12 {
   width: 75%;
}

.col-5-12 {
   width: 41.66%;
}

.col-7-12 {
   width: 58.33%
}

/* Pushing blocks */

.push-2-3, .push-8-12 {
   margin-left: 66.66%;
}

.push-1-2, .push-6-12 {
   margin-left: 50%;
}

.push-1-3, .push-4-12 {
   margin-left: 33.33%;
}

.push-1-4, .push-3-12 {
   margin-left: 25%;
}

.push-1-5 {
   margin-left: 20%;
}

.push-1-6, .push-2-12 {
   margin-left: 16.667%;
}

.push-1-7 {
   margin-left: 14.28%;
}

.push-1-8 {
   margin-left: 12.5%;
}

.push-1-9 {
   margin-left: 11.1%;
}

.push-1-10 {
   margin-left: 10%;
}

.push-1-11 {
   margin-left: 9.09%;
}

.push-1-12 {
   margin-left: 8.33%
}

@media handheld, only screen and (max-width: 767px) {
   .grid {
      width: 100%;
      min-width: 0;
      margin-left: 0px;
      margin-right: 0px;
      padding-left: 20px;
      /* grid-space to left */
      padding-right: 10px;
      /* grid-space to right: (grid-space-left - column-space) e.g. 20px-10px=10px */
   }
   [class*='col-'] {
      width: auto;
      float: none;
      margin-left: 0px;
      margin-right: 0px;
      margin-top: 10px;
      margin-bottom: 10px;
      padding-left: 0px;
      padding-right: 10px;
      /* column-space */
   }
   /* Mobile Layout */
   [class*='mobile-col-'] {
      float: left;
      margin-left: 0px;
      margin-right: 0px;
      margin-top: 0px;
      margin-bottom: 10px;
      padding-left: 0px;
      padding-right: 10px;
      /* column-space */
      padding-bottom: 0px;
   }
   .mobile-col-1-1 {
      width: 100%;
   }
   .mobile-col-2-3, .mobile-col-8-12 {
      width: 66.66%;
   }
   .mobile-col-1-2, .mobile-col-6-12 {
      width: 50%;
   }
   .mobile-col-1-3, .mobile-col-4-12 {
      width: 33.33%;
   }
   .mobile-col-1-4, .mobile-col-3-12 {
      width: 25%;
   }
   .mobile-col-1-5 {
      width: 20%;
   }
   .mobile-col-1-6, .mobile-col-2-12 {
      width: 16.667%;
   }
   .mobile-col-1-7 {
      width: 14.28%;
   }
   .mobile-col-1-8 {
      width: 12.5%;
   }
   .mobile-col-1-9 {
      width: 11.1%;
   }
   .mobile-col-1-10 {
      width: 10%;
   }
   .mobile-col-1-11 {
      width: 9.09%;
   }
   .mobile-col-1-12 {
      width: 8.33%
   }
   /* Layout Columns */
   .mobile-col-11-12 {
      width: 91.66%
   }
   .mobile-col-10-12 {
      width: 83.333%;
   }
   .mobile-col-9-12 {
      width: 75%;
   }
   .mobile-col-5-12 {
      width: 41.66%;
   }
   .mobile-col-7-12 {
      width: 58.33%
   }
   .hide-on-mobile {
      display: none !important;
      width: 0;
      height: 0;
   }
}



